}

.form-input {
  /* background-color: cornflowerblue; */
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  padding: 0.5rem 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
